import { DealsService } from './../deals.service';
import { FormGroup, FormControl, FormArray, FormBuilder } from '@angular/forms';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription, Observable } from 'rxjs';
import { Tier } from './tier.model';
import { AuthService } from '../auth/auth.service';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.css']
})
export class SettingsComponent implements OnInit, OnDestroy {
  settingsForm: FormGroup;
  tiersFormArray: FormArray;
  private authStatusSub: Subscription;
  hasBeenEdited = false;
  userIsAuthenticated = false;

  constructor(private formBuilder: FormBuilder, private dealsService: DealsService, private authService: AuthService) { }

  ngOnInit() {
    // set initial auth value
    this.userIsAuthenticated = this.authService.getIsAuth();

    // subscribe to auth changes
    this.authStatusSub = this.authService
      .getAuthStatusListener()
      .subscribe(isAuthenticated => {
        this.userIsAuthenticated = isAuthenticated;
      });


    this.settingsForm = this.formBuilder.group({
      'quota': '',
      'cloudBonus': '',
      'tiers': this.formBuilder.array([])
    });

    this.initForm();
  }


  createTier(min: number, max: number, rate: number): FormGroup {
    return this.formBuilder.group({
      min: [min],
      max: [max],
      rate: [rate]
    });
  }


  onAddTier(index: number) {
      this.tiersFormArray = this.settingsForm.get('tiers') as FormArray;
      this.tiersFormArray.insert(index + 1, this.createTier(0, 0, 0));
      this.hasBeenEdited = true;
  }

  onDeleteTier(index: number) {
    (<FormArray>this.settingsForm.get('tiers')).removeAt(index);
    this.hasBeenEdited = true;
  }


  private initForm() {
    this.hasBeenEdited = false;
    this.tiersFormArray = this.settingsForm.get('tiers') as FormArray;

    //clear form array to avoid concatination
    while (this.tiersFormArray.length !== 0) {
      this.tiersFormArray.removeAt(0);
    }

    this.settingsForm.reset();

    const tiers = this.dealsService.getTiers();

    tiers.forEach(tier => {
      if (tier) {
        this.tiersFormArray.push(this.createTier(tier.min, tier.max, tier.rate));
      }
    });

    let quota = this.dealsService.getQuota();
    let cloudBonus = this.dealsService.getCloudBonus();
    this.settingsForm.patchValue({'quota': quota, 'cloudBonus': cloudBonus});

  }

  onSave() {
      let tempArray: Tier[] = [];
      this.tiersFormArray = this.settingsForm.get('tiers') as FormArray;

      this.tiersFormArray.value.forEach(tier => {
          tempArray.push(tier);
      });

      this.dealsService.setQuota(this.settingsForm.get('quota').value);
      this.dealsService.setCloudBonus(this.settingsForm.get('cloudBonus').value);
      this.dealsService.updateTiers(tempArray);

      this.hasBeenEdited = false;

      this.initForm();

      // trigger API call to store settings if logged in
      if(this.userIsAuthenticated) {
        this.dealsService.storeSettingsToAPI();
      }
  }

  canDeactivate(): Observable<boolean> | boolean {
    if (this.hasBeenEdited || this.settingsForm.dirty) {

        if (window.confirm('Unsaved changes will be lost. Would you like to continue?')) {
          return true;
        }

        else {
          return false;
        }
    }
      return true;
    }


    ngOnDestroy() {
      this.authStatusSub.unsubscribe();
    }

}
