import { CanDeactivateGuard } from './can-deactivate-guard.service';
import { SettingsComponent } from './settings/settings.component';
import { DealsComponent } from './deals/deals.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './auth/login/login.component';
import { SignupComponent } from './auth/signup/signup.component';
import { AuthGuard } from './auth/auth.guard';

const routes: Routes = [
  { path: '', redirectTo: 'login', pathMatch: 'full'},
  { path: 'deals', component: DealsComponent, canDeactivate: [CanDeactivateGuard], canActivate: [AuthGuard]},
  { path: 'settings', component: SettingsComponent, canDeactivate: [CanDeactivateGuard], canActivate: [AuthGuard]},
  { path: 'login', component: LoginComponent},
  { path: 'signup', component: SignupComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
  providers: [CanDeactivateGuard, AuthGuard]
})
export class AppRoutingModule { }
