import { EventTriggerService } from './../../../shared/event-trigger.service';
import { FormGroup, FormControl } from '@angular/forms';
import { Deal } from './../../deal.model';
import { Component, OnInit, Input } from '@angular/core';
import { DealsService } from 'src/app/deals.service';

@Component({
  selector: '[app-deal]',
  templateUrl: './deal.component.html',
  styleUrls: ['./deal.component.css']
})
export class DealComponent implements OnInit {
  @Input() deal: Deal;
  @Input() index: number;
  dealStatuses = ['Forecast', 'Sold', 'Exclude'];
  statusDropDown: FormGroup;

  constructor(private dealsService: DealsService, private eventTriggerService: EventTriggerService) { }

  ngOnInit() {
    this.statusDropDown = new FormGroup({
      'status': new FormControl(this.deal.status)
    });

    this.statusDropDown.valueChanges.subscribe((val) => {
      this.onChangeStatus();
    });
  }

  onDelete() {
    this.dealsService.deleteDeal(this.index);
  }

  onEdit() {
    this.dealsService.editDealTrigger(this.index);
  }

  onChangeStatus() {
    // trigger recalculation
    this.dealsService.onEditStatus(this.index, this.statusDropDown.get('status').value);

    // trigger hasBeenEdited flag to show save changes button in deal list component
    this.eventTriggerService.dealHasBeenEdited();
  }
}
