export class Deal {
  public name: string;
  public value: number;
  public partnerCommission: number;
  public type: string;
  public commission: number;
  public cloudBonus: number;
  public incrementalComp: number;
  public status: string;


  // constructor(name: string, value: number, type: string, commission: number, cloudBonus: number, incrementalComp: number) {
  constructor(name: string, value: number, partnerCommission: number, type: string, status: string) {
      this.name = name;
      this.value = value;
      this.partnerCommission = partnerCommission;
      this.type = type;
      this.status = status;
      this.commission = 0;
      this.cloudBonus = 0;
      this.incrementalComp = 0;
  }
}
