import { DealListComponent } from './deal-list/deal-list.component';
import { DealsService } from 'src/app/deals.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Subscription, Observable } from 'rxjs';


@Component({
  selector: 'app-deals',
  templateUrl: './deals.component.html',
  styleUrls: ['./deals.component.css']
})
export class DealsComponent implements OnInit {
  subscription: Subscription;
  earnings = 0;
  attainment = 0;
  sales = 0;

  // gets dealList child element access for candeactivate function
  @ViewChild('dealList') dealList: DealListComponent;

  constructor(private dealsService: DealsService) {}

  ngOnInit() {
    this.subscription = this.dealsService.statsChanged.subscribe((stats: any) => {
      this.earnings = stats.totalEarnings;
      this.attainment = (stats.totalSales / stats.quota);
      this.sales = stats.totalSales;
    });

    const stats = this.dealsService.getStats();
    this.earnings = stats.totalEarnings;
    this.attainment = stats.totalSales / stats.quota;
    this.sales = stats.totalSales;
  }


  canDeactivate(): Observable<boolean> | boolean {
    if (this.dealList.hasBeenEdited) {

        if (window.confirm('Unsaved changes will be lost. Would you like to continue?')) {
          return true;
        }

        else {
          return false;
        }
    }
      return true;
    }
}



