import { Subscription } from 'rxjs';
import { Deal } from './../deal.model';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { DealsService } from '../../deals.service';
import { EventTriggerService } from 'src/app/shared/event-trigger.service';

@Component({
  selector: 'app-create-deal',
  templateUrl: './create-deal.component.html',
  styleUrls: ['./create-deal.component.css']
})
export class CreateDealComponent implements OnInit {
  newDealForm: FormGroup;
  types = ['Cloud', 'On Prem'];
  subscription: Subscription;
  editIndex: number = null;
  panelOpenState = false;
  statusOptions = ['Forecast', 'Sold', 'Exclude'];

  constructor(private dealsService: DealsService, private eventTriggerService: EventTriggerService) { }

  ngOnInit() {
    this.newDealForm = new FormGroup({
      'dealName': new FormControl(null, Validators.required),
      'value': new FormControl(null, Validators.required),
      'partnerCommission': new FormControl(),
      'type': new FormControl('Cloud', Validators.required),
      'status': new FormControl()
    });

    this.subscription = this.dealsService.editIndex.subscribe((index: number) => {
      let deals = this.dealsService.getDeals();
      this.seedEditForm(deals[index]);
      this.editIndex = index;
    });
  }

  //need to add status to all of these as well as maybe constructor
  onAdd() {
    let newDeal = new Deal(this.newDealForm.get('dealName').value, this.newDealForm.get('value').value,
    this.newDealForm.get('partnerCommission').value, this.newDealForm.get('type').value, 'Forecast');

    this.dealsService.addDeal(newDeal);
    this.newDealForm.reset();
    this.eventTriggerService.dealHasBeenEdited();
  }

  seedEditForm(deal: Deal) {
    this.newDealForm.setValue({dealName: deal.name, value: deal.value, partnerCommission: deal.partnerCommission,
      type: deal.type, status: deal.status});
    this.panelOpenState = true;
  }

//this needs to be saved without killing or hardcoding status value
  onSaveEdit() {
    let newDeal = new Deal(this.newDealForm.get('dealName').value, this.newDealForm.get('value').value,
    this.newDealForm.get('partnerCommission').value, this.newDealForm.get('type').value, this.newDealForm.get('status').value);

    this.dealsService.editDeal(this.editIndex, newDeal);
    this.editIndex = null;
    this.newDealForm.reset();
    this.panelOpenState = false;
  }

  onDiscardEdit() {
    this.newDealForm.reset();
    this.panelOpenState = false;
    this.editIndex = null;
  }
}
