import { AuthService } from './auth/auth.service';
import { ErrorService } from './error/error.service';
import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material';
import { ErrorComponent } from './error/error.component';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit{
  title = 'comm-calc';
  errorSubscription: Subscription;

  constructor(private dialog: MatDialog, private errorService: ErrorService, private authService: AuthService) {
  }


  ngOnInit() {
    this.authService.autoAuthUser();
    this.errorSubscription = this.errorService.errorCalled.subscribe((message: string) => {
      this.openDialog(message);
    });
  }


  openDialog(message: string) {
    const dialogConfig = new MatDialogConfig();

    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;

    dialogConfig.data = {
        message: message
    };

    this.dialog.open(ErrorComponent, dialogConfig);
}
}
