import { AuthService } from './../../auth/auth.service';
import { EventTriggerService } from './../../shared/event-trigger.service';
import { DealsService } from './../../deals.service';
import { Deal } from './../deal.model';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription, Observable } from 'rxjs';

@Component({
  selector: 'app-deal-list',
  templateUrl: './deal-list.component.html',
  styleUrls: ['./deal-list.component.css']
})
export class DealListComponent implements OnInit, OnDestroy {
  deals: Deal[];
  hasBeenEdited = false;
  dealChangeSubscription: Subscription;
  tierChangeSubscription: Subscription;
  editTriggerSubscription: Subscription;
  private authStatusSub: Subscription;
  userIsAuthenticated = false;

  constructor(private dealsService: DealsService, private eventTriggerService: EventTriggerService,  private authService: AuthService) { }

  ngOnInit() {
    // set initial auth value
    this.userIsAuthenticated = this.authService.getIsAuth();

    // subscribe to auth changes
    this.authStatusSub = this.authService
    .getAuthStatusListener()
    .subscribe(isAuthenticated => {
      this.userIsAuthenticated = isAuthenticated;
    });

    // get fresh data from API
    if(this.userIsAuthenticated) {
      this.dealsService.refreshDataFromApi();
    }

    // get new deals array (and recalculate) if deals change
    this.dealChangeSubscription = this.dealsService.dealsChanged.subscribe((deals: Deal[]) => {
      this.deals = deals;
    });

    // get new deals array (and recalculate) if tieres change
    this.tierChangeSubscription = this.dealsService.tiersChanged.subscribe((deals: Deal[]) => {
      this.deals = deals;
    });

    // catch event to show save changes button once changes have been made
    this.editTriggerSubscription = this.eventTriggerService.dealEditFlag.subscribe((flag: boolean) => {
      this.hasBeenEdited = flag;
    });

    this.deals = this.dealsService.getDeals();

  }

  // function to write back to API
  onSave() {
    this.hasBeenEdited = false;

    if(this.userIsAuthenticated) {
      this.dealsService.storeDealsToAPI();
    }
  }

  ngOnDestroy() {
    this.dealChangeSubscription.unsubscribe();
    this.tierChangeSubscription.unsubscribe();
    this.editTriggerSubscription.unsubscribe();
    this.authStatusSub.unsubscribe();
  }



  canDeactivate(): Observable<boolean> | boolean {
    console.log('here');
    if (this.hasBeenEdited) {

        if (window.confirm('YYYUnsaved changes will be lost. Would you like to continue?')) {
          return true;
        }

        else {
          return false;
        }
    }
      return true;
    }

}
